<template>
    <v-alert v-model="showAlert" :type="typeAlert" class="mt-0 text-xs-center">
        {{ message }}
    </v-alert>
</template>
<script>
export default {
  props: ['message', 'alertType'],
  computed: {
    typeAlert () {
      return this.alertType
    },
    showAlert () {
      return this.$store.getters.error !== null && typeof this.$store.getters.error !== 'undefined'
    }
  },
  mounted () {
    setTimeout(() => {
      this.$store.dispatch('clearError')
    }, 5000)
  }
}
</script>



