import $axios from '../helpers/axios'
export const actions = {
  signUserIn ({commit}, payload) {
    commit('setLoading', true)
    $axios.post(`${process.env.API}/login`, payload)
    .then(user => {
      console.log(user.data)
      if (!user.data.error) {
        const newUser = {
          id: user.data.datos.idUser,
          type: user.data.datos.userType,
          group: user.data.datos.idGroup,
          token: user.data.datos.Token,
          registerDate: user.data.datos.registeredAt
        }
        commit('setUser', newUser)
        localStorage.setItem('keepUpUserD4t4', JSON.stringify(newUser))
        commit('setLoading', false)
      } else {
        commit('setError', {message: user.data.message, type: 'error'})
        commit('setLoading', false)
      }
    })
    .catch(error => {
      commit('setLoading', false)
      commit('setError', {message: error.message, type: 'error'})
      console.log(error.message)
    })
  },
  signUserUp ({commit, state}, payload) {
    $axios.put(`${process.env.API}/signup/${state.user.id}`, payload)
    .then(userData => {
      if (!userData.data.error) {
        state.user.registerDate = userData.data.registerDate
      } else {
        commit('setError', {message: userData.data.message, type: 'error'})
      }
    })
    .catch(error => {
      commit('setError', {message: error.message, type: 'error'})
    })
  },
  autoSignIn ({commit}, payload) {
    commit('setLoading', true)
    let userData = localStorage.getItem('keepUpUserD4t4')
    if (userData) {
      let userDataJSON = JSON.parse(userData)
      const userLoggedIn = {
        id: userDataJSON.id,
        type: userDataJSON.type,
        group: userDataJSON.group,
        token: userDataJSON.token,
        registerDate: userDataJSON.registerDate
      }
      commit('setUser', userLoggedIn)
      setTimeout(() => {
        commit('setLoading', false)
      }, 1500)
    } else {
      commit('setLoading', false)
    }
  },
  userLogOut ({commit}) {
    localStorage.removeItem('keepUpUserD4t4')
    commit('setUser', null)
  },
  clearError ({commit}) {
    commit('setError', null)
  },
  addNewSubject ({commit, state}, payload) {
    commit('setLoading', true)
    $axios.post(`${process.env.API}/addsubjects/${state.user.id}`, payload)
    .then(subjectData => {
      console.log(subjectData.data)
      if (!subjectData.data.error) {
        const newSubject = {
          Id_subject: subjectData.data.idSubject,
          Name: payload.subjectName,
          SubjectImage: payload.subjectImage,
          Description: payload.Description
        }
        commit('addNewSubject', newSubject)
      } else {
        alert(subjectData.data.message)
        commit('setLoading', false)
      }
    })
    .catch(error => {
      console.log(error)
      commit('setLoading', false)
    })
  },
  loadAllSubjects ({commit, state}) {
    commit('setLoading', true)
    $axios.get(`${process.env.API}/getallsubjects/${state.user.id}`)
    .then(allSubjectsData => {
      commit('setLoading', false)
      commit('loadAllSubjects', allSubjectsData.data)
    })
    .catch(error => {
      console.log(error.message)
      commit('setLoading', false)
    })
  }
}



