<template>
    <v-container fluid class="pa-3">
        <v-layout row class="mb-4">
            <v-flex xs12>
                <h1 class="text-xs-center">Sign Up. Let's have fun!</h1>
            </v-flex>
        </v-layout>
        <v-layout align-center justify-center>
            <v-flex xs12 sm8 md5>
                <form @submit.prevent="signUserUp">
                    <v-card class="elevation-3">
                        <v-card-text>
                            <v-layout row>
                                <v-flex xs12>
                                    <v-text-field
                                    autofocus
                                    v-model="names"
                                    name="names"
                                    v-validate="'required'"
                                    prepend-icon="face"
                                    label="Your names">
                                    </v-text-field>
                                    <span
                                    v-show="errors.has('names')">
                                    {{ errors.first('names') }}
                                    </span>
                                </v-flex>
                            </v-layout>
                            <v-layout row>
                                <v-flex xs12>
                                    <v-text-field
                                    v-model="lastnames"
                                    name="lastnames"
                                    v-validate="'required'"
                                    prepend-icon="face"
                                    required
                                    label="Your last names">
                                    </v-text-field>
                                    <span
                                    v-show="errors.has('lastnames')">
                                    {{ errors.first('lastnames')}}
                                    </span>
                                </v-flex>
                            </v-layout>
                            <v-layout row>
                                <v-flex xs12>
                                    <v-text-field
                                    v-model="email"
                                    name="email"
                                    v-validate="'required|email'"
                                    prepend-icon="email"
                                    type="email"
                                    label="Your email address">
                                    </v-text-field>
                                    <span
                                    v-show="errors.has('email')">
                                    {{ errors.first('email')}}
                                    </span>
                                </v-flex>
                            </v-layout>
                            <v-layout row>
                                <v-flex xs12>
                                    <v-text-field
                                    v-model="newUser"
                                    name="newUser"
                                    prepend-icon="accessibility_new"
                                    id="newUser"
                                    required
                                    label="Your new user">
                                    </v-text-field>
                                </v-flex>
                            </v-layout>
                            <v-layout row>
                                <v-flex xs12>
                                    <v-text-field
                                    v-model="newPassword"
                                    name="newPassword"
                                    prepend-icon="lock"
                                    id="newPassword"
                                    required
                                    label="Your new password"
                                    type="password">
                                    </v-text-field>
                                </v-flex>
                            </v-layout>
                            <v-layout row>
                                <v-flex xs12>
                                    <v-text-field
                                    v-model="confirmNewPassword"
                                    name="confirmNewPassword"
                                    prepend-icon="lock"
                                    id="confirmNewPassword"
                                    required
                                    label="Confirm your new password"
                                    type="password"
                                    :rules="[comparePaswwords]">
                                    </v-text-field>
                                </v-flex>
                            </v-layout>                       
                        </v-card-text>
                        <v-card-actions>
	                        <v-btn block type="submit" color="primary">
                                Register
                            </v-btn>
	                    </v-card-actions>
                    </v-card>
                </form>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
export default {
  data () {
    return {
      names: '',
      lastnames: '',
      email: '',
      newUser: '',
      newPassword: '',
      confirmNewPassword: ''
    }
  },
  methods: {
    signUserUp () {
      const user = {
        names: this.names,
        lastnames: this.lastnames,
        email: this.email,
        newUser: this.newUser,
        newPassword: this.newPassword
      }
      this.$store.dispatch('signUserUp', user)
    }
  },
  computed: {
    comparePaswwords () {
      return this.newPassword !== this.confirmNewPassword ? 'Passwords DO NOT match' : ''
    },
    checkUserId () {
      return this.$store.getters.user.registerDate
    }
  },
  watch: {
    checkUserId (value) {
      if (value !== null && typeof value !== 'undefined') {
        this.$router.push('/student/home')
      }
    }
  }
}
</script>



