<template>
	<v-container fluid fill-height>
        <v-layout align-center justify-center>          
          <v-flex xs12 sm8 md4>
          	<form @submit.prevent="login">
	            <v-card class="elevation-3">
	              <v-card-text>
	                  <v-text-field 
                    required
                    autofocus 
                    prepend-icon="person" 
                    name="login" 
                    label="User" 
                    type="text" 
                    v-model="user"></v-text-field>
	                  <v-text-field 
                    required
                    id="password" 
                    prepend-icon="lock" 
                    name="password" 
                    label="Password" 
                    type="password" 
                    v-model="password"></v-text-field>
	              </v-card-text>
	              <v-card-actions>
	                <!-- <v-spacer></v-spacer> -->
	                <!-- <v-btn type="submit" block="" color="primary">Login</v-btn> -->
                  <v-btn
                  :disabled="!isValid"
                  type="submit"
                  color="primary"
                  block
                >
                  Login
                </v-btn>
	              </v-card-actions>
	            </v-card>
            </form>
          </v-flex>
        </v-layout>
      </v-container>
</template>

<script>
  export default {
    data () {
      return {
        user: '',
        password: ''
      }
    },
    methods: {
      login () {
        this.$store.dispatch('signUserIn', {user: this.user, password: this.password})
      }
    },
    computed: {
      isValid () {
        return this.user.trim() !== '' && this.password.trim() !== ''
      }
    }
  }
</script>


