<template>
    <v-dialog v-model="addSubjectDialog" max-width="580px" persistent>
        <v-btn
        outline
        color="primary"
        slot="activator">
        <v-icon left>add</v-icon>
        Add New Subject
        </v-btn>
        <v-card>
            <v-progress-linear v-model="uploadProgress" class="pa-0 mb-0 mt-0"></v-progress-linear>
            <v-form @submit.prevent="onSaveNewSubject">
                <v-card-title class="pb-0">
                    <span class="headline">Add a new subject</span>
                </v-card-title>
                <v-card-text>
                    <v-container fluid class="pa-1">
                        <v-layout row>
                            <v-flex xs12>
                                <v-text-field
                                required
                                autofocus
                                v-model="newSubject"                                
                                label="New Subject's Name">
                                </v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-layout row>
                            <v-flex xs12>
                                <v-textarea
                                v-model="description"                             
                                label="Description"
                                no-resize
                                maxlength="100"
                                counter>
                                </v-textarea>
                            </v-flex>
                        </v-layout>
                        <v-layout row>
                            <v-flex xs12 class="text-xs-right">                                
                                <input type="file" ref="fileInput" style="display:none;" @change="chooseFile" accept="image/*">
                                <v-btn @click="$refs.fileInput.click()" outline>
                                    <v-icon left>attach_file</v-icon>
                                    Add Image
                                </v-btn>
                            </v-flex>
                        </v-layout>      
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-btn 
                    outline
                    color="red darken-2"
                    @click="addSubjectDialog = false"
                    block>
                        <v-icon left>cancel</v-icon>
                        Cancelar
                    </v-btn>
                    <v-btn
                    outline
                    color="blue darken-3"
                    type="submit"
                    block
                    :disabled="!isValid">
                    <v-icon left>save</v-icon>
                    Guardar
                    </v-btn>
                </v-card-actions>
            </v-form>            
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      addSubjectDialog: false,
      newSubject: '',
      subjectImage: 'subjectDefault.jpeg',
      uploadProgress: 0,
      choosenFile: '',
      description: ''
    }
  },
  computed: {
    isValid () {
      return this.newSubject.trim() !== ''
    }
  },
  methods: {
    onSaveNewSubject () {
      this.$store.dispatch('addNewSubject', {subjectName: this.newSubject, subjectImage: this.subjectImage, Description: this.description})
      this.newSubject = ''

      if (this.choosenFile !== '') {
        let fd = new FormData()
        fd.append('image', this.choosenFile)
        fd.append('imageName', this.subjectImage)
        axios.post(`${process.env.API}/uploadimagesubject`, fd, {
          onUploadProgress: progress => {
            this.uploadProgress = Math.round(progress.loaded / progress.total * 100)
          }
        })
        .then(uploadedFileData => {
          console.log(uploadedFileData.data)
          this.subjectImage = 'subjectDefault.jpeg'
          this.choosenFile = ''
          setTimeout(() => {
            this.uploadProgress = 0
          }, 2000)
        })
        .catch(error => {
          console.log(error.message)
        })
      }
      this.addSubjectDialog = false
    },
    chooseFile (event) {
      this.choosenFile = event.target.files[0]
      this.subjectImage = event.target.files[0].name
    }
  }
}
</script>




