<template>
    <v-dialog v-model="dialog" persistent max-width="500px">
        <v-btn block flat slot="activator">Add a new subject</v-btn>
        <v-form @submit.prevent="saveSubject">
            <v-card>
                <v-card-title class="text-xs-center">
                    <span class="headline">Create a new subject</span>
                </v-card-title>
                <v-card-text>
                    
                        <v-container>
                            <v-layout>
                                <v-flex xs12>
                                    <v-text-field
                                    autofocus
                                    v-validate="'required'"
                                    label="New subject's name"
                                    name="subject" 
                                    color="white"
                                    v-model="subjectName">                
                                    </v-text-field>
                                    <span class="red--text darken-1" v-show="errors.has('subject')">
                                        {{ errors.first('subject') }}
                                    </span>
                                </v-flex>
                            </v-layout>
                        </v-container>
                    
                </v-card-text>
                <v-card-actions class="text-xs-center">
                    <v-btn depressed block color="red darken-1" @click="dialog = false">
                        <v-icon left>cancel</v-icon>
                        Cancel
                    </v-btn>
                    <v-btn 
                    depressed 
                    block 
                    type="submit" 
                    color="blue darken-1" 
                    :disabled="!subjectNameIsEmpty || loading"
                    :loading="loading">
                        <v-icon left>save</v-icon>
                        Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-form>
    </v-dialog>
</template>

<script>
export default {
  data () {
    return {
      dialog: false,
      subjectName: ''
    }
  },
  computed: {
    subjectNameIsEmpty () {
      return this.subjectName.trim() !== ''
    },
    loading () {
      return this.$store.getters.loading
    }
  },
  methods: {
    saveSubject () {
      this.$store.dispatch('addNewSubject', {subjectName: this.subjectName})
    }
  }
}
</script>




