<template>
  <v-app>
    <v-navigation-drawer
      temporary
      v-model="drawer"
      enable-resize-watcher
      fixed
      app
    >
      <v-list class="pa-1">
        <v-list-tile avatar>
          <v-list-tile-avatar>
            <img src="https://randomuser.me/api/portraits/men/85.jpg" >
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title>Alex Galicia</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
      <v-list class="pt-0" dense>
        <v-divider></v-divider>
        <v-list-tile v-for="item in menuItems" :key="item.titulo" :to="item.link">
          <v-list-tile-action>
            <v-icon>{{ item.icono }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.titulo }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click="logUserOut">
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Logout</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      app
      class="primary elevation-0"
      dark
    >
      <v-toolbar-side-icon class="hidden-sm-and-up" @click.stop="drawer = !drawer" v-if="userIsLoggedIn"></v-toolbar-side-icon>
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only" v-if="userIsLoggedIn">
        <v-btn v-for="item in menuItems" :key="item.titulo" flat :to="item.link">
          <v-icon left>{{ item.icono }}</v-icon>
          {{ item.titulo }}
        </v-btn>
        <v-btn flat @click="logUserOut">
          <v-icon left>exit_to_app</v-icon>
          Logout
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-content>
      <loader v-if="loading" mode="in-out"/>
      <transition name="bounce">
        <alert :message="error.message" :alertType="error.type" v-if="error"/>
      </transition>
      <transition name="slide-fade" mode="out-in">
        <router-view/>
      </transition>
    </v-content>
  </v-app>
</template>

<script>
export default {
  data () {
    return {
      title: 'KeepApp',
      drawer: false
    }
  },
  computed: {
    menuItems () {
      let menuItems = []
      if (this.userType === 1) {
        this.$router.push('/admin/home')
      } else if (this.userType === 2) {
        menuItems = [
          {titulo: 'Home', icono: 'home', link: '/teacher/home'},
          {titulo: 'My Subjects', icono: 'library_books', link: '/teacher/subjects'},
          {titulo: 'My Groups', icono: 'person', link: '/teacher/groups'}
        ]
        this.$router.push('/teacher/home')
      } else if (this.userType === 4) {
        if (this.userRegisterDate) {
          menuItems = [
            {titulo: 'Home', icono: 'home', link: '/student/home'},
            {titulo: 'My Subjects', icono: 'library_books', link: '/student/subjects'},
            {titulo: 'Profile', icono: 'person', link: '/student/profile'}
          ]
          this.$router.push('/student/home')
        } else {
          this.$router.push('/user/signup')
        }
      }
      return menuItems
    },
    userType () {
      if (this.$store.getters.user !== null && typeof this.$store.getters.user !== 'undefined') {
        return this.$store.getters.user.type
      }
    },
    userIsLoggedIn () {
      return this.$store.getters.user !== null && typeof this.$store.getters.user !== 'undefined'
    },
    userCheck () {
      return this.$store.getters.user
    },
    userRegisterDate () {
      return this.$store.getters.user.registerDate !== null && typeof this.$store.getters.user.registerDate !== 'undefined'
    },
    loading () {
      return this.$store.getters.loading
    },
    error () {
      return this.$store.getters.error
    }
  },
  methods: {
    logUserOut () {
      this.$store.dispatch('userLogOut')
    }
  },
  watch: {
    userCheck (value) {
      if (value === null || typeof value === 'undefined') {
        this.$router.push('/')
      }
    }
  },
  name: 'App'
}
</script>

<style scoped>
  .slide-fade-enter-active {
      transition: all .2s ease;
    }
    .slide-fade-leave-active {
      transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to {
      transform: translateX(10px);
      opacity: 0;
    }

    .bounce-enter-active {
      animation: bounce-in .5s;
    }
    .bounce-leave-active {
      animation: bounce-in .5s reverse;
    }
    @keyframes bounce-in {
      0% {
        transform: scale(0);
      }
      50% {
        transform: scale(1.5);
      }
      100% {
        transform: scale(1);
      }
    }
        
</style>




