<template>
<v-container>
    <v-layout row class="mb-3">
        <v-flex xs12>
            <h1>My Subjects</h1>
        </v-flex>
    </v-layout>
    <v-layout class="mb-3">
        <v-flex xs12>
            <v-divider></v-divider>      
        </v-flex>
    </v-layout>
    <v-layout row class="mb-3">
        <v-flex xs12 class="pa-1 text-xs-right">
            <addSubjectDialog />
        </v-flex>
    </v-layout>
    <v-layout row wrap>
        <v-flex xs12 sm6 md4 v-for="subject in subjects" :key="subject.Id_subject" class="pl-2 pr-2 mb-3">
            <v-card class="elevation-3">
                <v-card-media :src="'../static/images/' + subject.SubjectImage" height="200px"></v-card-media>
                <v-card-title primary-title>
                    <div>
                        <h3 class="headline mb-0">{{ subject.Name }}</h3>
                        <div>{{ subject.Description }}</div>
                    </div>
                </v-card-title>
                <v-card-actions>
                    <v-btn 
                    flat 
                    color="grey darken-1">
                    <v-icon>group</v-icon>
                        Groups
                    </v-btn>
                    <v-btn 
                    flat 
                    color="grey darken-1">
                    <v-icon>folder</v-icon>
                    Content
                    </v-btn>
                    <v-btn
                    title="Delete"
                    icon>
                    <v-icon color="red">delete</v-icon>
                    </v-btn>
                    <v-btn
                    icon
                    title="Edit">
                    <v-icon color="orange">edit</v-icon>
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
</v-container>
</template>

<script>
export default {
  data () {
    return {
      modal: false
    }
  },
  computed: {
    subjects () {
      return this.$store.getters.subjects
    }
  },
  created () {
    this.$store.dispatch('loadAllSubjects')
  }
}
</script>




