import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/user/login'
import SignUp from '@/components/user/signup'
import homeStudent from '@/components/student/home'
import homeAsisstant from '@/components/assistant/home'
import homeAdmin from '@/components/admin/home'
import homeTeacher from '@/components/teacher/home'
import teacherSubjects from '@/components/teacher/subjects'

// Guard
import Guard from './guard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/user/signup',
      name: 'SignUp',
      component: SignUp,
      beforeEnter: Guard
    },
    {
      path: '/student/home',
      name: 'studentHome',
      component: homeStudent,
      beforeEnter: Guard
    },
    {
      path: '/teacher/subjects',
      name: 'teacherSubjects',
      component: teacherSubjects,
      beforeEnter: Guard
    },
    {
      path: '/teacher/home',
      name: 'teacherHome',
      component: homeTeacher
    },
    {
      path: '/admin/home',
      name: 'adminHome',
      component: homeAdmin
    },
    {
      path: '/assistant/home',
      name: 'assistantHome',
      component: homeAsisstant
    }
  ],
  mode: 'history'
})



