// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import Vuex from 'vuex'
import Axios from 'axios'
import { store } from './store'
import VeeValidate from 'vee-validate'
import 'vuetify/dist/vuetify.min.css'
import loader from '@/components/shared/loader.vue'
import Alert from '@/components/shared/alert.vue'
import addSubjectDialog from '@/components/teacher/addSubjectDialog.vue'

// Components
import addSubject from '@/components/dialogs/addSubject.vue'

Vue.component('addSubject', addSubject)

Vue.use(Vuetify, { theme: {
  primary: '#FE4B4B',
  secondary: '#424242',
  accent: '#82B1FF',
  error: '#FF5252',
  info: '#2196F3',
  success: '#4CAF50',
  warning: '#FFC107'
}})

Vue.use(Vuex)
Vue.use(VeeValidate)
Vue.prototype.$axios = Axios
Vue.config.productionTip = false

// Componentes
Vue.component('loader', loader)
Vue.component('addSubjectDialog', addSubjectDialog)
Vue.component('alert', Alert)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  computed: {
    getUser () {
      return this.$store.getters.user !== null && typeof this.$store.getters.user !== 'undefined'
    }
  },
  mounted () {
    if (!this.getUser) {
      this.$store.dispatch('autoSignIn')
    } else {
      this.$router.push('/')
    }
  }
})



